* {
  margin: 0;
  padding: 0;
}

html,
body,
#application {
  height: 100%;
  overflow: hidden;
}

.diagram-container {
  background: #333333;
  width: 100%;
  height: 100%;
}

.custom-node {
  border: solid 2px gray;
  border-radius: 5px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  position: relative;
}

.custom-node-color {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  transform: translate(-50%, -50%);
  border-radius: 10px;
}

.circle-port {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #4472c4;
  border: 2px solid #2f528f;
  right:0;
  transform: translate(50%);
  z-index: -1;
  
 
}
.circle-port--input {

	left:0;
	transform: translate(-50%);
	z-index: -1;
	
   
  }

.circle-port:hover {
  background: mediumpurple;
}
